.trial {
  width: 50%;

  @include vp-1200 {
    width: 100%;
  }
}

.trial__container {
  background-color: $color-primary;
  color: $color-default-white;
  padding: 56px 170px 69px 103px;
  min-height: 100%;

  @include vp-1200 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 60px 163px 70px 133px;
    min-height: auto;
  }

  @include vp-767 {
    padding: 58px 15px 68px;
  }
}

.trial__title {
  @include section-title;
  padding: 0;
  margin: 0;
  margin-bottom: 17px;

  @include vp-1200 {
    margin-bottom: 32px;
  }

  @include vp-767 {
    margin-bottom: 30px;
  }
}

.trial__form-input-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 28px;

  @include vp-767 {
    margin-bottom: 6px;
    flex-direction: column;
  }

  label {
    width: 46%;

    @include vp-767 {
      width: 100%;
    }

    @include vp-767 {
      margin-bottom: 32px;
    }
  }
  input {
    @include small-text;
    width: 100%;
    background-color: $color-transparent;
    color: $color-default-white;
    border: none;
    border-bottom: 1px solid #74819c;
    padding: 0;
    margin: 0;
    padding-bottom: 3px;

    &:hover:not(:disabled) {
      border-bottom: 1px solid $color-default-white;
    }

    &:focus:not(:disabled),
    &:active:not(:disabled) {
      outline: none;
      border: 1px solid $color-default-white;
    }

    &:disabled {
      opacity: 0.3;
      cursor: auto;
    }
  }
}

.trial__form-button {
  padding: 17px;
  font-size: $text-size-small;
  line-height: $text-size-small;
  width: 100%;
}
